import React, { useEffect, useMemo } from "react";
import { Row, Col } from "antd";
import Paper from "../../components/Paper/Paper";
import ForgotPasswordForm from "../../screens/AuthScreens/ForgotPasswordForm";
import AuthLeftPanel from "./components/AuthLeftPanel";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import AuthRightPanel from "./components/AuthRightPanel";
import WithImageWraper from "../../components/Common/WithImageWraper";
import { useDispatch } from "react-redux";
import { setAppLoader } from "../../../redux/common/CommonActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const ForgotPasswordPage = () => {
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    dispatch(setAppLoader(false));
  }, []);

  return (
    <WithImageWraper>
      <div className="login-page-container">
        <div className="w-60 vh-70 overflow-hidden br-4">
          <Paper className={"h-100"}>
            <Row className="h-100">
              <Col lg={10}>
                <AuthLeftPanel
                  link={AUTH_ROUTES.REGISTER}
                  linkText={t('translation:forgotPassword.authLeft.link')}
                  title={t('translation:forgotPassword.authLeft.title')}
                />
              </Col>
              <Col lg={14} className={"h-100 background-white py-20"}>
                <AuthRightPanel
                  title={t('translation:forgotPassword.authRight.title')}
                  description={t('translation:forgotPassword.authRight.desc')}
                >
                  <ForgotPasswordForm />
                </AuthRightPanel>
              </Col>
            </Row>
          </Paper>
        </div>
      </div>
    </WithImageWraper>
  );
};

export default ForgotPasswordPage;
