import { Col, Row, Button as AButton, Form } from "antd";
import { useDispatch, useSelector } from "react-redux";
import React, { useEffect, useMemo } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import IconFormInput from "../../components/Inputs/IconFormInput";
import SvgConverter from "../../components/Common/SvgConverter";
import { ReactComponent as MailIcon } from "../../../assets/icons/mail.svg";
import { ReactComponent as ArrowLeftIcon } from "../../../assets/icons/arrow-left.svg";
import {
  forgotPasswordInProgress,
  resetForgotPassword,
} from "../../../redux/auth/AuthActions";
import WarningModal from "../../components/Modals/WarningModal";
import { uniqueID } from "../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const LoginForm = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { forgotPassword } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);


  const onSubmit = (data) => {
    dispatch(forgotPasswordInProgress(data));
  };

  useEffect(() => {
    return () => {
      dispatch(resetForgotPassword());
    };
  }, []);

  return (
    <Form onFinish={onSubmit} className={"mt-40"}>
      <IconFormInput
        editMode={true}
        className="mt-20"
        name={"email"}
        rules={[
          { required: true, message: t('translation:forgotPassword.form.email.enter') },
          { type: "email", message: t('translation:default.notification.email')},
        ]}
        placeholder={t('translation:forgotPassword.form.email.placeholder')}
        icon={
          <SvgConverter
            Icon={MailIcon}
            color={"white"}
            width={25}
            height={20}
          />
        }
        type={"text"}
      />

      <AButton
        id={uniqueID()}
        type="primary"
        loading={forgotPassword.progressing}
        htmlType="submit"
        className="background-header mt-30 w-100 br-4"
      >
        <span className="auth-button-labels mt-n2">{t("translation:forgotPassword.form.button")}</span>
      </AButton>

      <Row justify={"center"} className={"mt-15"}>
        <Col>
          <Link
            id={uniqueID()}
            to={AUTH_ROUTES.LOGIN}
            type="link"
            size={"small"}
            className="text-header"
          >
            <span className="text-header border-bottom-header fw-600 f-16">
              <SvgConverter Icon={ArrowLeftIcon} color="header" /> {t("translation:forgotPassword.form.backToLogin")}
            </span>
          </Link>
        </Col>
      </Row>
      <WarningModal
        open={forgotPassword.success}
        description={
          t("translation:forgotPassword.form.warning.desc")
        }
        id={uniqueID()}
        okText={t("translation:forgotPassword.form.warning.okText")}
        onOk={() => navigate(AUTH_ROUTES.LOGIN)}
        onClose={() => navigate(AUTH_ROUTES.LOGIN)}
      />
    </Form>
  );
};

export default LoginForm;
