import React, {useMemo} from "react";
import { Form, Input } from "antd";
import { isRequiredField } from "../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const IconFormInput = ({
  placeholder = false,
  label,
  editMode = false,
  forFilter = false,
  margin = "mb-15",
  singleLineMode = false,
  name = `testFiled-${Math.random()}`,
  rules = [],
  colon = false,
  inverMode = false,
  disabled,
  onBlur,
  className = "",
  inputType = "text",
  icon,
  type = "text",
  enableSuffix = false,
  onChange = false,
  readOnly = false,
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <>
      <Form.Item
        className={`${inverMode ? "invert-mode" : ""} ${margin} ${
          singleLineMode ? "single-line-mode" : ""
        } form-input-container ${
          !editMode ? "edit-mode-off" : ""
        } ${className}`}
        name={name}
        rules={rules}
        {...(label
          ? {
              label: (
                <span>
                  {label}
                  {rules.length > 0 && isRequiredField(rules) && (
                    <span className="required">*</span>
                  )}
                </span>
              ),
            }
          : {})}
        labelCol={10}
        colon={colon}
      >
        {type === "password" ? (
          <Input.Password
            id={name}
            prefix={icon}
            disabled={disabled}
            readOnly={!editMode}
            placeholder={
              editMode ? (placeholder ? placeholder : `${t('translation:default.fields.enter')} ${label}`) : ""
            }
            className={`form-icon-input`}
            {...(onBlur ? { onBlur } : {})}
            type={inputType}
          />
        ) : (
          <Input
            id={name}
            prefix={!enableSuffix && icon}
            disabled={disabled}
            readOnly={readOnly ? readOnly : !editMode}
            placeholder={
              editMode ? (placeholder ? placeholder : `${t('translation:default.fields.enter')} ${label}`) : ""
            }
            className={
              !enableSuffix ? `form-icon-input` : `input-form-filter-suffix`
            }
            {...(onBlur ? { onBlur } : {})}
            type={inputType}
            suffix={enableSuffix && icon}
            {...(onChange ? { onChange } : {})}
          />
        )}
      </Form.Item>
    </>
  );
};

export default IconFormInput;
